/*----------------------------------------
Transforms */
.membership-comparison-details .card {
  position: relative;
  display: block;
  background-color: transparent !important;
  border: 0 !important;
  word-wrap: break-word;
  overflow-wrap: break-word;
}
.membership-comparison-details .btn {
  display: inline-block;
  position: relative;
  font-weight: bold;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  color: #22588E;
  background-color: #C8D7E7;
  border: 2px solid transparent;
  padding: 0.375rem 1rem;
  font-size: 0.75rem;
  line-height: 1.2;
  border-radius: 0;
  transition: color 0.2s ease-in-out, background-color 0.2s ease-in-out, border-color 0.2s ease-in-out;
  letter-spacing: 0.25em;
}
.membership-comparison-details .btn:focus, .membership-comparison-details .btn:hover {
  background-color: #fff;
  color: #22588E;
}
.membership-comparison-details .btn-primary {
  display: inline-block;
  position: relative;
  font-weight: bold;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  color: #fff;
  background-color: #22588E;
  border: 2px solid transparent;
  padding: 0.375rem 1rem;
  font-size: 0.75rem;
  line-height: 1.2;
  border-radius: 0;
  transition: color 0.2s ease-in-out, background-color 0.2s ease-in-out, border-color 0.2s ease-in-out;
  letter-spacing: 0.25em;
}
.membership-comparison-details .btn-primary:focus, .membership-comparison-details .btn-primary:hover {
  background-color: #fff;
  color: #22588E;
}

.bg-blue-grad-1 {
  background: #2a5e99;
  background: linear-gradient(180deg, #2a5e99 0%, #bbc6d3 60%, #ffffff 100%);
  background: -moz-linear-gradient(180deg, #2a5e99 0%, #bbc6d3 60%, #ffffff 100%);
}

.card-header-design {
  background-image: url("data:image/svg+xml;base64,");
  background-size: 14%;
  background-position: 50% 80%;
  background-repeat-x: repeat;
  background-repeat-y: no-repeat;
  min-height: 3rem;
  border-radius: 8px 8px 0 0;
  -webkit-border-radius: 8px 8px 0 0;
  -moz-border-radius: 8px 8px 0 0;
}

.dark-bg {
  background-color: #252525;
}

.light-bg {
  background-color: #eceff7;
}

.text-grey-11 {
  color: #eceff7;
}

.signature-comparison-main {
  background: #252525;
  background: radial-gradient(ellipse farthest-side at center center, #464646 0%, #3a3a3a 20%, #242424 100%);
  background: -moz-radial-gradient(ellipse farthest-side at center center, #464646 0%, #3a3a3a 20%, #242424 100%);
  padding: 0 1.5rem !important;
  color: #fff;
  font-weight: 500;
}
.signature-comparison-main h2, .signature-comparison-main h3, .signature-comparison-main h4, .signature-comparison-main h5, .signature-comparison-main h6 {
  color: #fff !important;
}

.standard-comparison-main {
  background: #ebeff6;
  padding: 0 1.5rem !important;
  color: #333333;
  font-weight: 500;
}

.card-footer-design {
  background-image: url("data:image/svg+xml;base64,");
  background-size: 14%;
  background-position: 0 60%;
  background-repeat-x: repeat;
  background-repeat-y: no-repeat;
  min-height: 3rem;
  border-radius: 0 0 8px 8px;
  -webkit-border-radius: 0 0 5px 5px;
  -moz-border-radius: 0 0 8px 8px;
}